<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid p-3">
        <div class="row m-1 bg-light py-2 px-1">
          <div class="col-12 col-sm-4">
            <label>انتخاب محصول</label>
            <Multiselect
              class="my-1"
              v-model="productId"
              mode="single"
              :searchable="true"
              :createTag="true"
              :options="productOptions"
            />
          </div>
          <div class="col-12 col-sm-4">
            <template v-if="sesssionsList">
              <label>انتخاب جلسه</label>
              <Multiselect
                class="my-1"
                v-model="product_detail_videos_id"
                mode="single"
                :searchable="true"
                :createTag="true"
                :options="sesssionsList"
              />
            </template>
          </div>
          <div class="col-12 col-sm-4 pt-4">
            <input
              id="showOnlines"
              v-model="showOnlines"
              type="checkbox"
              class="form-check-input"
            />
            <label class="form-check-label mx-2" for="showOnlines"
              >نمایش آنلاین‌ها</label
            >

            <input
              id="showOfflines"
              v-model="showOfflines"
              type="checkbox"
              class="form-check-input"
            />
            <label class="form-check-label mx-2" for="showOfflines"
              >نمایش آفلاین‌ها</label
            >
          </div>
          <div class="col-12 text-start">
            <button class="btn btn-primary" @click="getReport">جستجو</button>

            <span v-show="showSpin">
              <i class="fas fa-spinner fa-spin mt-3"></i>
            </span>
          </div>
        </div>

        <table class="table table-info">
          <thead>
            <tr class="table-dark">
              <th>ردیف</th>
              <th>کاربر</th>
              <th>نام کاربری</th>
              <template v-if="showOnlines">
                <th>ورود-آنلاین</th>
                <th>آخرین حضور-آنلاین</th>
                <th>زمان-دقیقه-آنلاین</th>
              </template>

              <template v-if="showOfflines">
                <th>ورود-آفلاین</th>
                <th>آخرین حضور-آفلاین</th>
                <th>زمان-دقیقه-آفلاین</th>
              </template>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item, index) in reports" :key="item.id">
              <tr>
                <td>{{ index + 1 }}</td>
                <td>{{ item.user.first_name }} {{ item.user.last_name }}</td>
                <td>{{ item.user.email }}</td>
                <template v-if="showOnlines">
                  <td>{{ item.online_started_at }}</td>
                  <td>{{ item.online_exited_at }}</td>
                  <td>
                    {{
                      calcTime(
                        item.online_started_at,
                        item.online_exited_at,
                        item.online_spend
                      )
                    }}
                  </td>
                </template>

                <template v-if="showOfflines">
                  <td>{{ item.offline_started_at }}</td>
                  <td>{{ item.offline_exited_at }}</td>
                  <td>{{ item.offline_spend }}</td>
                </template>
              </tr>
            </template>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";
import Multiselect from "@vueform/multiselect";
import utils from "../libs/utils.js";
import jmoment from "moment-jalaali";

export default {
  name: "PresenceReport",
  created() {
    this.arefApi = new ArefApi();
    this.utils = new utils();
    this.jmoment = jmoment;
    this.getProductsList();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
    Multiselect,
  },
  data() {
    return {
      productsList: null,
      productOptions: null,
      productId: null,
      product_detail_videos_id: this.$route.params.product_detail_videos_id
        ? this.$route.params.product_detail_videos_id
        : null,
      showSpin: false,
      reports: null,
      sesssionsList: null,
      showOnlines: true,
      showOfflines: false,
    };
  },
  methods: {
    getProductsList() {
      this.arefApi.getProductsIndex(1, true).then((res) => {
        this.productsList = res.data;
        this.productsList = this.productsList.filter((item) => {
          if (item.type == "video") {
            return item;
          }
        });
        this.productOptions = this.productsList.map((item) => {
          return { value: item.id, label: item.name };
        });
      });
    },
    getProductVideoSessions() {
      this.arefApi
        .getProductVideoSessions(this.productId, 1, true)
        .then((sesssionsList) => {
          this.sesssionsList = sesssionsList.data.map((item) => {
            return { value: item.id, label: item.name };
          });
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },

    getReport() {
      if (!this.product_detail_videos_id) {
        this.$swal.fire({
          title: "!خطا",
          text: `جلسه را انتخاب نمایید`,
          icon: "warning",
          confirmButtonText: "متوجه شدم",
        });
        return;
      }
      this.showSpin = true;
      this.arefApi
        .presenceReport(this.product_detail_videos_id)
        .then((resp) => {
          let reports = resp.data;
          reports = reports.map((item) => {
            item.offline_exited_at = item.offline_exited_at
              ? this.jmoment(item.offline_exited_at).format(
                  "jYYYY/jMM/jDD-HH:mm"
                )
              : "";

            item.offline_started_at = item.offline_started_at
              ? this.jmoment(item.offline_started_at).format(
                  "jYYYY/jMM/jDD-HH:mm"
                )
              : "";

            item.online_exited_at = item.online_exited_at
              ? this.jmoment(item.online_exited_at).format(
                  "jYYYY/jMM/jDD-HH:mm"
                )
              : "";

            item.online_started_at = item.online_started_at
              ? this.jmoment(item.online_started_at).format(
                  "jYYYY/jMM/jDD-HH:mm"
                )
              : "";
            if (item.user) {
              item.user.first_name = item.user.first_name
                ? item.user.first_name
                : "";
              item.user.last_name = item.user.last_name
                ? item.user.last_name
                : "";
            } else {
              item.user = {
                first_name: "پیدا نشد",
                last_name: "پیدا نشد",
              };
            }
            return item;
          });
          reports.sort(function (a, b) {
            return b.online_spend - a.online_spend;
          });
          this.reports = reports;
        })
        .catch((err) => {
          console.log(err);
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          this.showSpin = false;
        });
    },

    calcTime(start, end, spend) {
      if (!end || !start) {
        return spend;
      }

      start = jmoment(start).unix();
      end = jmoment(end).unix();
      if (end <= start) {
        return 0;
      }
      const calculatedTime = +parseInt((end - start) / 60);
      return Math.min(calculatedTime, spend);
    },
  },
  watch: {
    productId: function (newVal) {
      this.sesssionsList = null;
      this.product_detail_videos_id = null;
      if (!newVal) {
        return;
      }
      const foundProduct = this.productsList.find((item) => item.id == newVal);
      if (foundProduct.type == "video") {
        this.getProductVideoSessions();
      }
    },
  },
};
</script>
<style src="@vueform/multiselect/themes/default.css"></style>